/**
 * @description 
 *  ua  init  checkDarkModeFunc
 * 
 * @returns {boolean} is dark mode result
 */
function defaultCheckDarkModeFunc() {
  var HX_THEME_KEYWORD = 'hxtheme';
  var userAgent = window.navigator.userAgent;
  var keywordIndex = userAgent.indexOf(HX_THEME_KEYWORD);
  if (keywordIndex > -1) {
    var keywordOffset = keywordIndex + 8;
    var hxthemeValue = userAgent.substring(keywordOffset, keywordOffset + 1);
    if (hxthemeValue && Number(hxthemeValue) === 1) {
      return true;
    }
  }
  return false;
}

/**
 * @description 
 * @param {boolean} isDarkMode 
 */
function addLightOrDarkClass(isDarkMode) {
  var HTML_CLASS = {
    light: 'light',
    dark: 'dark'
  };
  document.documentElement.classList.remove(isDarkMode ? HTML_CLASS.light : HTML_CLASS.dark);
  document.documentElement.classList.add(isDarkMode ? HTML_CLASS.dark : HTML_CLASS.light);
}

/**
 * 
 * @param {*} options { checkDarkModeFunc: () => {}, darkMode: true }
 */
function initTheme(options) {
  if (options === void 0) {
    options = {};
  }
  var defaults = {
    /**
     * 
     */
    darkMode: true,
    /**
     * 
     */
    checkDarkModeFunc: defaultCheckDarkModeFunc
  };
  var settings = Object.assign({}, defaults, options);
  var darkMode = settings.darkMode;
  if (darkMode) {
    var checkDarkModeFunc = settings.checkDarkModeFunc;
    var isDarkMode = checkDarkModeFunc();
    addLightOrDarkClass(isDarkMode);
  }

  // const isCssVarsCompatible = checkCssVarsCompatibility();
  // if (!isCssVarsCompatible) {
  //   cssVars();
  // }
  cssVars();
}

export { initTheme };
